import Vue from "vue";
import Router from "vue-router";
import UserConfig from "./views/UserConfig.vue";
import MenuConfig from "./views/MenuConfig.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/UserConfig",
      name: "UserConfig",
      component: UserConfig
    },
    {
      path: "/MenuConfig",
      name: "MenuConfig",
      component: MenuConfig
    }
  ]
});
